import Axios from 'axios'
import React, { useState, useEffect } from 'react'
import './App.css'
import Questionaire from './components/Questionaire'
import Options from './components/Options'

const API_URL =
	'https://opentdb.com/api.php?amount=10&category=18&difficulty=easy'

function App() {
	const [questions, setQuestions] = useState([])
	const [currentIndex, setCurrentIndex] = useState(0)
	const [score, setScore] = useState(0)
	const [showAnswers, setShowAnswers] = useState(false)
	const [value, setValue] = useState('easy')
	const [url, setUrl] = useState(API_URL)

	useEffect(() => {
		Axios.get(url)
			.then((res) => res.data)
			.then((data) => {
				const questions = data.results.map((question) => ({
					...question,
					answers: [
						question.correct_answer,
						...question.incorrect_answers,
					].sort(() => Math.random() - 0.5),
				}))
				setQuestions(questions)
			})
	}, [])

	const handleAnswer = (answer) => {
		if (!showAnswers) {
			if (answer === questions[currentIndex].correct_answer) {
				setScore(score + 1)
			}
		}

		setShowAnswers(true)
	}

	const handleNextQuestion = () => {
		//console.log(currentIndex)
		if (currentIndex > 9) {
			console.log(currentIndex)
			setCurrentIndex(0)
		} else {
			setCurrentIndex(currentIndex + 1)
			setShowAnswers(false)
		}
	}

	const handlechange = (event) => {
		const difficulty = event.target.value
		setValue(difficulty)
		if (difficulty === 'easy') {
			setUrl(
				'https://opentdb.com/api.php?amount=10&category=18&difficulty=easy'
			)
		} else if (difficulty === 'medium') {
			setUrl(
				'https://opentdb.com/api.php?amount=10&category=18&difficulty=medium'
			)
		} else if (difficulty === 'hard') {
			setUrl(
				'https://opentdb.com/api.php?amount=10&category=18&difficulty=hard'
			)
		}
	}

	return questions.length > 0 ? (
		<div className='container'>
			{currentIndex >= questions.length ? (
				<>
					<h1 className='bg-Info p-5 rounded-3'>
						Game Ended, Your Score is {score}
					</h1>
					<button onClick={handleNextQuestion} className='next-question'>
						start-again
					</button>
				</>
			) : currentIndex === 0 ? (
				<div>
					<h1 className='text-center align-self-start'>
						Computer Science Quiz
					</h1>
					<div className='questionClass d-flex flex-row justify-content-between mb-5'>
						<label className='label mr-5' htmlFor='category'>
							Difficulty Level :
						</label>
						<div className='control ml-5'>
							<div className='select '>
								<Options handlechange={handlechange} value={value} />
							</div>
						</div>
					</div>
					<div className='d-flex justify-content-center'>
						<button
							value={value}
							onClick={handleNextQuestion}
							className='next-question  p-4'>
							Start Quiz
						</button>
					</div>
				</div>
			) : (
				<Questionaire
					handleAnswer={handleAnswer}
					showAnswers={showAnswers}
					handleNextQuestion={handleNextQuestion}
					data={questions[currentIndex]}
				/>
			)}
		</div>
	) : (
		<div className='container'>Loading...</div>
	)
}

export default App
